
import tabGroup from '@/components/group/tab'
import detialList from '@/components/cart/detiallist'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import anNiu from '@/components/group/anniu'

export default {
components: {
detialList: detialList,
tabGroup: tabGroup,
CheckIcon,
anNiu: anNiu,
Confirm
},
data () {
return {
list1: [
{ name: '1', img: '', cost: 1, check: false, attribute: 'ok' },
{ name: '1', img: '', cost: 2, check: false, attribute: 'ok' }
],
title: '选货',
allcheck: false,
a: true,
tishi: '是否删除该收藏？',
b: '去支付 '
}
},
props: {},
computed: {},
methods: {
quanxuan () {
if (this.allcheck === true) {
for (let a = 0; a < this.list1.length; a++) {
this.list1[a].check = true
}
} else {
for (let a = 0; a < this.list1.length; a++) {
this.list1[a].check = false
}
}
},
detele () {
this.show = true
}

}
}



